/*===============================================
================== VARIABLE =====================
=================================================*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap");
:root {
  --ff-primary: "Roboto", sans-serif;
  font-size: 16px;
  --fw-light: 300;
  --fw-regular: 400;
  --fw-bold: 700;
  --fs-300: 0.875rem;
  --fs-400: 0.9375rem;
  --fs-600: clamp(1.125rem, 1.875vw, 1.6875rem);
  --fs-700: clamp(1.5rem, 1.736vw, 2.5rem);
  --fs-900: clamp(1.6875rem, 3.056vw, 2.75rem);
  --clr-dark: rgb(0, 0, 0);
  --clr-white: hsl(0 0% 100%);
  --clr-accent-40: hsl(7, 100%, 60%);
  --clr-accent-30: hsl(7, 100%, 70%);
  --clr-neutral-40: hsl(0, 0%, 88%);
  --bg-primary: var(--clr-neutral-40);
  --fg-primary: var(--clr-dark);
  --line-height-regular: 1;
  --line-height-midtall: 1.2;
  --line-height-tall: 1.5;
  --padding-600: clamp(0.125rem, 3.06vw, 2.75rem);
  --padding-900: clamp(0.125rem, 9.028vw, 8.125rem);
  --transition-time-fast: 250ms;
  --transition-time-slow: 550ms;
  --transiiton-func: ease-in-out;
  --transition-fast: var(--transition-time-fast) var(--transiiton-func);
  --transition-slow: var(--transition-time-slow) var(--transiiton-func);
}

/*===============================================
================ RESET CLASSES ==================
===============================================*/
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
ol {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1;
}

a,
a:not([class]) {
  text-decoration: none;
}

img,
picture {
  max-width: 100%;
  display: block;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/* ============ Scrollbar Styling ================ */
* {
  scrollbar-width: 16px;
  scrollbar-color: var(--clr-accent-40) var(--clr-accent-30);
}

*::-webkit-scrollbar {
  width: 16px;
}

*::-webkit-scrollbar-track {
  background: var(--clr-accent-30);
}

*::-webkit-scrollbar-thumb {
  background-color: var(--clr-accent-40);
  border-radius: 20px;
  border: 2px solid var(--clr-accent-40);
}

/*===============================================
================ MAIN LAYOUT ====================
===============================================*/
/*===============================================
================ UTILITY CLASSES ================
=================================================*/
.bg-primary {
  background-color: var(--bg-primary);
}

.fg-primary {
  color: var(--fg-primary);
}

.backdrop {
  width: 100%;
  height: 100vh;
  position: fixed;
  inset: 0;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(3px);
}

.container {
  width: min(100%, 90rem);
  margin-inline: auto;
  padding-inline: var(--padding-inline, 1rem);
}

.container-fluid {
  padding-inline: 0;
}

.nav-fixed {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 99;
  background-color: hsla(0deg, 0%, 100%, 0.7);
  backdrop-filter: blur(2px);
}

.flex {
  display: flex;
  gap: var(--gap, 1rem);
}

.flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--gap, 1rem);
}

.grid {
  display: grid;
  gap: var(--gap, 1rem);
}

.align-center {
  align-items: center;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.hidden {
  display: none;
}

.strap {
  background-color: var(--clr-dark);
  color: var(--clr-white);
}

.text-center {
  text-align: center;
}

.icon {
  width: 24px;
  height: 24px;
}

.btn {
  appearance: none;
  border: none;
  display: inline-block;
  text-align: center;
  font: inherit;
  line-height: 1;
  background-color: var(--clr-accent-40);
  color: var(--clr-white);
  padding-block: var(--btn-padding-block, 0.5rem);
  padding-inline: var(--btn-padding-inline, 1.25rem);
  border-radius: var(--radius, 0.625rem);
  transition: all var(--trasition-slow);
}
@media (hover: hover) {
  .btn:is(:hover, :focus) {
    background-color: var(--clr-white);
    color: var(--clr-accent-40);
    border: 2px solid var(--clr-accent-40);
    transition: all var(--trasition-slow);
  }
}

a:focus {
  outline: 2px dotted var(--clr-accent-40);
  outline-offset: 3px;
}

.link {
  display: inline-block;
  color: inherit;
  font-size: var(--fs-300);
  letter-spacing: 1px;
  transition: color var(--transition-fast);
}
@media (hover: hover) {
  .link:is(:hover, :focus) {
    color: var(--clr-accent-40);
  }
}

.carousel {
  width: 100%;
  height: 18.75rem;
  position: relative;
}
.carousel__btn {
  cursor: pointer;
  width: 2.25rem;
  height: 2.25rem;
  background-color: var(--clr-neutral-40);
  border-radius: 50%;
  font-size: 1.25rem;
  position: relative;
  z-index: 10;
}
.carousel__btn .arrow {
  width: 40%;
  height: 40%;
  background-color: transparent;
  border-right: 2px solid var(--clr-accent-40);
  border-bottom: 2px solid var(--clr-accent-40);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (hover: hover) {
  .carousel__btn:is(:hover, :focus) {
    border: 2px dotted var(--clr-accent-40);
  }
}
.carousel .btn-left {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%) rotate(135deg);
}
.carousel .btn-left .arrow {
  left: 25%;
  transform: translateY(-65%);
}
.carousel .btn-right {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%) rotate(-45deg);
}

.slide {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 350ms;
}
@media only screen and (min-width: 44em) {
  .slide {
    width: 50%;
  }
}
@media only screen and (min-width: 63em) {
  .slide {
    width: 33.33%;
  }
}

body {
  background-color: var(--bg-primary, hsl(0deg, 0%, 90%));
  color: var(--fg-primary, #1a1a1e);
  font-family: var(--ff-primary, monospace);
  font-size: var(--fs-400, 1rem);
}

p,
span,
a {
  line-height: var(--line-height-tall);
}

.root {
  --padding-inline: 2.25em;
  --padding-block: 0.975em;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.hero {
  --btn-padding-inline: 2rem;
  --btn-padding-block: 0.625rem;
  --gap: 0;
  position: relative;
}
.hero__text {
  text-align: center;
  padding-block-start: 1.25rem;
  padding-inline: var(--padding-600);
}
.hero__text h1,
.hero__text h2 {
  margin-block-end: 0.5rem;
  font-size: var(--fs-900);
}
.hero__text p {
  max-width: 72ch;
  line-height: var(--line-height-tall);
}
.hero__links {
  --btn-padding-inline: 1.325rem;
  --btn-padding-block: 0.625rem;
  --gap: 0.625rem;
  margin-block-start: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: var(--gap, 1rem);
}
@media only screen and (min-width: 44em) {
  .hero__links {
    --btn-padding-inline: 2rem;
    --btn-padding-block: 0.625rem;
  }
}
@media only screen and (min-width: 63em) {
  .hero__text {
    padding-block: 0;
    position: absolute;
    top: clamp(1.25rem, 3.472vw, 3.125rem);
    left: 50%;
    transform: translateX(-50%);
  }
}

.img__group {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  gap: var(--gap, 1.25rem);
}
.img__container {
  position: relative;
}
.img__container img {
  max-height: var(--img-height, 26.25rem);
}
.img__container .img-content {
  width: 100%;
  padding-block: 0.625rem;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2px);
  color: var(--clr-white);
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.img__container .img-content h2, .img__container .img-content h3, .img__container .img-content h4 {
  font-size: var(--fs-600);
  font-weight: var(--fw-regular);
  line-height: var(--line-height-tall);
}
@media (hover: hover) {
  .img__container .img-content p:is(:hover, :focus),
  .img__container .img-content span:is(:hover, :focus) {
    text-decoration: underline;
  }
}

/*================ Header Styling ================*/
.fin-option {
  font-size: 0.75rem;
  padding-block: 0.5rem;
}
.fin-option p {
  text-transform: uppercase;
  letter-spacing: 1px;
}

.header {
  --padding-inline: clamp(0.625rem, 2.36vw, 2.425rem);
  --gap: 0.625rem;
  --radius: 0.75rem;
  position: relative;
}
.header__nav {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  width: 10rem;
}
.header__logo a {
  display: block;
}
.header__logo a img {
  margin-block: var(--padding-block);
}
.header__navbar {
  margin-inline: auto;
}
.header__navbar [data-menu=close] {
  display: none;
}
.header__navbar .nav {
  height: 100%;
  display: flex;
  align-items: center;
  gap: var(--gap, 1rem);
}
.header__navbar .nav-link {
  text-transform: uppercase;
}
@media only screen and (max-width: 62.99em) {
  .header__navbar {
    background-color: var(--clr-neutral-40);
    width: 100%;
    position: absolute;
    z-index: 99;
    left: 0;
    top: max(-100vh, -100vw);
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.3);
    transition: all var(--transition-slow);
  }
  .header__navbar.open {
    top: 0;
    transition: all var(--transition-slow);
  }
  .header__navbar [data-menu=close] {
    display: block;
  }
  .header__navbar [data-menu=close] img {
    position: absolute;
    width: 32px;
    height: 32px;
    right: 30px;
    top: 15px;
    border: 2px solid black;
  }
  .header__navbar .nav {
    flex-direction: column;
    padding-block: 3.125rem 1.25rem;
  }
  .header__navbar .nav-item {
    padding-block: 0.625rem;
  }
  .header__navbar .nav-link {
    display: block;
    text-align: center;
  }
}
.header__shopping {
  --gap: 0.625em;
  margin-inline-start: auto;
}
.header [data-menu=open] {
  display: none;
}
.header [data-menu=open] img {
  border: 2px solid black;
}
@media only screen and (max-width: 62.99em) {
  .header [data-menu=open] {
    display: block;
  }
}
.header .hero__brands {
  margin-block-start: 1.25rem;
  padding-block: 1.25rem;
  padding-inline: 3.125rem;
  display: flex;
  column-gap: clamp(1rem, 8.889vw, 8rem);
  row-gap: 1.25rem;
  align-items: center;
  justify-content: center;
  flex-flow: wrap;
}
.header .hero__brands img {
  max-width: 11.25rem;
}

/*================ Main Styling ================*/
.main {
  --margin-block: 1.625rem;
}
.main > * {
  margin-block-start: var(--margin-block);
}
.main .boosted-options {
  --padding-inline: var(--padding-600);
}
@media only screen and (min-width: 63em) {
  .main .boosted-options {
    --padding-inline: var(--padding-900);
  }
}
.main .boosted__content {
  margin-block-end: 1.25rem;
  text-align: center;
}
.main .boosted__content h2 {
  font-size: var(--fs-700);
  padding-block-end: 0.625rem;
}
.main .boosted__content p {
  max-width: 72ch;
  margin-inline: auto;
  line-height: var(--line-height-tall);
}
.main .boosted-sketboards .hero__text {
  padding-block: 0 1.25rem;
}
.main .boosted-sketboards .hero__text h2 {
  padding-block: 0.625rem;
}
.main .boosted-revs {
  --padding-inline: var(--padding-600);
}
@media only screen and (min-width: 63em) {
  .main .boosted-revs {
    --padding-inline: var(--padding-900);
  }
}
.main .revs {
  display: grid;
  grid-template-columns: minmax(0, 1fr);
  grid-template-rows: auto;
  gap: 0.75rem;
}
@media only screen and (min-width: 44em) {
  .main .revs {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media only screen and (min-width: 63em) {
  .main .revs {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}
.main .revs > * {
  justify-self: center;
}
.main .revs img {
  width: 100%;
}
.main .revs__details {
  margin-block: auto;
}
@media only screen and (min-width: 63em) {
  .main .revs__details {
    margin-inline-start: 0.75rem;
  }
}
.main .revs__details h2 {
  font-size: 2rem;
  font-weight: var(--fw-regular);
}
.main .revs__details p {
  padding-block-end: 0.75rem;
  line-height: var(--line-height-tall);
}
.main .revs__details .btn {
  --padding-block: 0.5rem;
  --padding-inline: 1.125rem;
  width: max-content;
  margin-inline: auto;
}
@media only screen and (max-width: 62.99em) {
  .main .revs__details {
    grid-row: 1/2;
    grid-column: 1/3;
  }
}
@media only screen and (max-width: 43.99em) {
  .main .revs__details {
    grid-row: 1/2;
    grid-column: 1/2;
  }
}
.main .boosted-shop {
  --img-height: 30rem;
  --gap: 0.5rem;
}
@media only screen and (min-width: 44em) {
  .main .boosted-shop .img__group {
    justify-content: space-between;
    flex-flow: row nowrap;
  }
}
.main .boosted-features {
  --gap: clamp(1rem, 2.778vw, 2.5rem);
  background-color: var(--clr-dark);
  color: var(--clr-white);
  padding-block: 1.25rem;
}
.main .boosted-features .flex {
  justify-content: center;
  flex-wrap: wrap;
}
@media only screen and (min-width: 44em) {
  .main .boosted-features .flex {
    flex-wrap: nowrap;
  }
}
.main .boosted-features .feature {
  max-width: 36ch;
}
.main .boosted-features .feature-icon {
  width: 5rem;
  height: 5rem;
}
.main .boosted-features .feature h2,
.main .boosted-features .feature h3 {
  font-size: var(--fs-700);
}
.main .boosted-accessories {
  --padding-inline: var(--padding-600);
  background-color: var(--clr-white);
}
.main .boosted-accessories h2 {
  font-size: var(--fs-900);
  padding-block: 1.875rem 3.125rem;
  border-bottom: 2px solid var(--bg-primary);
}
.main .accessory {
  --gap: 0.625rem;
}
.main .accessory h4 {
  font-size: var(--fs-600);
  color: var(--clr-accent-40);
}
.main .price .old {
  color: rgba(0, 0, 0, 0.8);
  text-decoration: line-through;
}
.main .price .new {
  font-size: 110%;
  color: var(--clr-accent-40);
  font-weight: var(--fw-bold);
}
.main .price .sold {
  color: rgba(0, 0, 0, 0.8);
}

/*================ Footer Styling ================*/
.footer__wraper {
  padding-block: 1.875rem;
  padding-inline: var(--padding-600);
  display: grid;
  gap: 1.625rem;
  grid-template-columns: 1fr;
  justify-items: center;
}
@media only screen and (min-width: 44em) {
  .footer__wraper {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (min-width: 63em) {
  .footer__wraper {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}
.footer__logo {
  max-width: 10rem;
}
.footer__links h3 {
  font-size: var(--fs-900);
  font-weight: var(--fw-regular);
  margin-block-end: 1rem;
}
.footer__links ul {
  display: flex;
  flex-flow: row wrap;
}
.footer__links ul li {
  width: 50%;
}
.footer__links ul li a {
  padding-block: 0.625rem;
}
.footer__info {
  text-align: left;
}
.footer__info h2 {
  font-size: 1.25rem;
  font-weight: var(--fw-regular);
}
.footer__info.flex-column {
  align-items: flex-start;
}
@media only screen and (min-width: 44em) {
  .footer__info {
    grid-column: span 2;
  }
}
@media only screen and (min-width: 63em) {
  .footer__info {
    grid-column: span 1;
  }
}
.footer__credit {
  --padding-inline: var(--padding-600);
  --gap: 0.425rem;
  background-color: white;
  padding-block: 1.25rem;
}
.footer__credit .flex {
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
}
.footer__credit .flex .flex-column {
  --gap: 0;
}
.footer__credit .credit-cards {
  margin-inline: auto;
}
@media only screen and (min-width: 44em) {
  .footer__credit .credit-cards {
    margin-inline-end: 0;
  }
}
.footer__credit .credit-cards img {
  width: 2.625rem;
  height: 1.75rem;
  border: 2px solid #bbb;
  border-radius: 0.375rem;
}

/*===============================================
================ BLOCK LEVEL ====================
=================================================*/
h1 {
  font-size: var(--fs-700);
  font-weight: var(--fw-700);
  line-height: var(--line-height-tall);
}